#bio {
  color: #fff;
  background: lighten($mainColor, 10);

  .flex-wrapper {
    margin: auto;
    width: $container;
    @include flex;
    align-items: center;

    .bio-img {
      width: 250px;
      border-radius: 50%;
      padding: $gutterWidth; }

    .bio-text {
      text-align: center;
      @include letter-spacing;
      font-size: 1.3rem;
      line-height: 1.5rem; } } }
